:root {
  --primary-link-color: #000;
  --second-link-color: #00acdc;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.header {
  align-items: center;
  padding: 0 50px;
  display: flex;
}

body {
  font-family: Roboto, Raleway, sans-serif;
}

.logo {
  margin: 0;
  padding: 0;
  display: block;
}

.nav {
  margin-left: auto;
}

.logo__image {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  display: block;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  margin-left: 20px;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__link {
  white-space: nowrap;
  text-transform: uppercase;
  color: var(--primary-link-color);
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__link:hover {
  color: var(--second-link-color);
}

.nav__link.is-active {
  color: #00acdc;
}

.nav__link.is-active:after {
  content: "";
  background-color: var(--second-link-color);
  border-radius: 2px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}
